/* Navigation Bar */
/* styles.css */

/* Navbar */
.container-fluid{
  padding: 60px;
  background-color: rgb(236, 230, 222); /* Default background color for container */
  width: 1000px;
  height: 1200px;
  position:relative;
}
.navbar {
  display: flex;
  justify-content: space-between; 
  align-items:center; 
  padding: 20px 30px; 
  background-color: #e8d8d8;
  text-decoration: none;
 /* Default background color for navbar */
}

.navbar img {
  height: 40px;
}

.navbar ul {
  display: flex;
  margin: 0;
  padding: 0;
}

.navbar li {
  list-style-type: none;
  margin-left: 40px;
}

.navbar a {
  text-decoration: none;
  color: #123f6c; /* Default color for navbar links */
  font-weight: bold;
}

.navbar a:hover {
  color: #007bff; /* Color for navbar links on hover */
}

/* home.css */

.background-image {  
  width: 1000px;
  height: 1000px; 
  margin-bottom: 20px; 
  position:relative; 
}

.background-image img {
  width: 1000px;
  height: 600px;
  object-fit: cover;
  position: absolute; /* Add position absolute */
  top: 0; /* Align to top */
  left: 0; /* Align to left */
}

h2 {
  font-size: 24px; /* Adjust font size as needed */
  margin-bottom: 20px; /* Adjust margin as needed */
}
/* App.css */
.form-inline {
  display: flex;
  align-items: center;
}

.form-control {
  width: 300px; /* Adjust width as needed */
  margin-right: 10px; /* Adjust margin as needed */
  
}

.btn {
  padding: 8px 16px; /* Adjust padding as needed */
  font-size: 16px; /* Adjust font size as needed */
}


